  flex: none;
  margin-bottom: 0.5rem;
}
@media (min-width: 2000px) {
  .social-block__list li {
    margin-bottom: 5px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__list li {
    margin: 0 1.25rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__list li {
    margin: 0 1.25rem;
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: #ffffff;
  border: 2px solid transparent;
  border-radius: 50%;
  opacity: 0;
  transition-property: all;
  transition-timing-function: linear;
  transition-duration: 200ms;
  bottom: -20px;
}
@media (min-width: 2000px) {
  .social-block__link {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__link {
    color: #ffffff;
    background-color: #5f458c;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__link {
    color: #ffffff;
    background-color: #5f458c;
  }
}
.social-block__link:hover {
  color: #a67ee5;
}
body.second .social-block__link:hover {
  color: #56a1ff;
}
body.third .social-block__link:hover {
  color: #66bcd6;
}
body.fourth .social-block__link:hover {
  color: #ca7af3;
}
.social-block__link:focus {
  color: #a67ee5;
  background-color: #ffffff;
  border-color: #a67ee5;
  outline: none;
}
.social-block__link:active, .social-block__link:active:focus {
  color: #a67ee5;
  background-color: #ffffff;
  border-color: #ffffff;
}
.social-block__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.social-block__link--fb svg {
  width: 0.8rem;
  height: 1.7rem;
}
@media (min-width: 2000px) {
  .social-block__link--fb svg {
    width: 8px;
    height: 17px;
  }
}
.social-block__link--insta svg {
  width: 1.5rem;
  height: 1.5rem;
}
@media (min-width: 2000px) {
  .social-block__link--insta svg {
    width: 15px;
    height: 15px;
  }
}
.social-block__link--vk svg {
  width: 1.7rem;
  height: 1rem;
}
@media (min-width: 2000px) {
  .social-block__link--vk svg {
    width: 10px;
    height: 10px;
  }
}

.social-block--active .social-block__link {
  opacity: 1;
  bottom: 0;
}
.social-block--active .social-block__link--fb {
  transition-delay: 150ms;
}
.social-block--active .social-block__link--insta {
  transition-delay: 250ms;
}
.social-block--active .social-block__link--vk {
  transition-delay: 350ms;
}

.animation-text__word {
  display: inline-block;
  padding: 5px 0 0;
  overflow: hidden;
}
.animation-text__word > span {
  display: inline-block;
  transform: translate3d(0, 1.5em, 0);
}

.screen.active .animation-text__word > span {
  transform: translate3d(0, 0, 0);
}

.intro__title > .animation-text__word {
  letter-spacing: -8px;
}
.intro__title > .animation-text__word:first-child {
  padding: 26px 0 0;
}

.intro__date.active-animation > .animation-text__word > span.space {
  width: 10px;
}